<template>
    <div id="header"  class="d-flex justify-content-between" 
        style="border-bottom: solid 1px #ccc; box-shadow: 0 6px 4px -1px rgb(216, 216, 216);">
        <div class="img-header">
            <router-link to="/home">
                <img alt="AngerPulse logo mini" class="p-2 imgHome" style="height: 60px;"  v-if="showImage" @click="$router.push({ name: 'Home' })"
                    src="../assets/btnAngerPulseHeader.png">
            </router-link>
        </div>
        <div class="menu-header d-flex">
            <div class="m-auto">
                <label style="cursor: pointer;"   @click="$i18n.locale = 'en'">
                    {{ t('info.En') }} /
                </label>
            </div>
            <div class="m-auto ps-2 pe-2" >
                <label style="cursor: pointer;" @click="$i18n.locale = 'es'">
                    {{ t('info.Es') }} 
                </label>
            </div>
            <div v-if="isLoggedIn" data-bs-toggle="modal" data-bs-target="#modalMenu">
                <img alt="AngerPulse logo mini" style="width: 40px; height: 100%"
                    src="../assets/menu.svg">
            </div>
        </div>
    </div>

    <!-- Modal -->
    <div v-if="LanguageSpanish" class="modal fade right-slide" data-bs-scroll="true" id="modalMenu" tabindex="-1" aria-labelledby="exampleModalLabel"
        aria-hidden="true">
        <div class="modal-dialog modal-dialog-scrollable">
            <div class="modal-content">
                <div class="modal-body">
                    <div class="dv-menu" data-bs-dismiss="modal">
                        <router-link to="/dashboard" class="comfortaa-menu" v-html="t('menu.actividad')"></router-link>
                    </div>
                    <div class="dv-menu" data-bs-dismiss="modal">
                        <router-link to="/audioteca" class="comfortaa-menu" v-html="t('menu.audioteca')"></router-link>
                    </div>
                    <div class="dv-menu" data-bs-dismiss="modal">
                        <router-link to="/configuracion" class="comfortaa-menu" v-html="t('menu.audios')"></router-link>
                    </div>
                    <div class="dv-menu" data-bs-dismiss="modal">
                        <router-link to="/perfil" class="comfortaa-menu" v-html="t('menu.perfil')"></router-link>
                    </div>
                    <div class="dv-menu" data-bs-dismiss="modal">
                        <router-link to="/plan" class="comfortaa-menu" v-html="t('menu.pagos')"></router-link>
                    </div>                    
                     <div class="dv-menu" data-bs-dismiss="modal">
                        <a class="comfortaa-menu" v-html="t('menu.logout')" @click="logout()"></a>
                    </div>
                    <!-- BOT√ìN CIRCULAR PARA DESPLEGAR M√ÅS -->
                    <div v-if="!showMore" class="text-center mt-3" @click="showMore = true" style="margin:15px">
                       <a class="comfortaa-menu">Gu√≠a Angerpulse</a> 
                    </div>
                    <div v-if="showMore" class="extra-options guia">
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info' }" class="comfortaa-menu" v-html="t('menu.ira')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Angerpulse' }"  class="comfortaa-menu" v-html="t('menu.angerpulse')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Quien' }" class="comfortaa-menu" v-html="t('menu.para')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Como' }" class="comfortaa-menu" v-html="t('menu.como')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Cuando' }" class="comfortaa-menu" v-html="t('menu.cuando')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Dashboard' }" class="comfortaa-menu" v-html="t('menu.actividad_guia')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Audioteca' }" class="comfortaa-menu" v-html="t('menu.audioteca_guia')"></router-link>
                        </div>
                        <div class="dv-menu" data-bs-dismiss="modal">
                            <router-link :to="{ path: '/info', hash: '#Config' }" class="comfortaa-menu" v-html="t('menu.audios_guia')"></router-link>
                        </div>
                    </div>
                    <button v-if="!showMore" class="btn" @click="toggleMore" style="background-color: #fff; border-radius: 50%; left: 44%; position: fixed; top:442px; width: 45px; height: 45px;">
                        <img alt="AngerPulse logo mini" src="../assets/arrow-down.svg"> </button>
                     <button v-else class="btn" @click="toggleMore" style="background-color: #fff; border-radius: 50%; left: 44%; position: fixed; top:372px; width: 45px; height: 45px;">
                        <img alt="AngerPulse logo mini" style="transform: rotate(180deg);" src="../assets/arrow-down.svg"> </button>
                </div>
            </div>
        </div>
    </div>
      
    <div class="content">
        <router-view></router-view>
    </div>
</template>

<script setup>
    import { ref, watch, onMounted } from 'vue';
    import { useRoute, useRouter } from 'vue-router'
    import { useI18n } from 'vue-i18n'

    const router = useRouter()    
    const { t } = useI18n()
    const route = useRoute()

    const isLoggedIn = ref(false);
    const showImage = ref(true)
    const LanguageSpanish = ref(true)
    const showMore = ref(false);

    const toggleMore = () => {
        showMore.value = !showMore.value
    }

    onMounted(() => {
        const token = localStorage.getItem('token')
        isLoggedIn.value = token !== null && token !== '' && token !== 'undefined'
    });

    watch(
        () => route.path,
        (newPath) => { 
            showImage.value = newPath !== '/home' && newPath !== '/' , 
            isLoggedIn.value = localStorage.getItem('token') !== null && localStorage.getItem('token') !== '' && localStorage.getItem('token') !== 'undefined'
         },
        { immediate: true }
    )
   
    const logout = () => {
        localStorage.removeItem('token')
        isLoggedIn.value = false;
        router.push('/login') 
    }

    document.addEventListener("hidden.bs.modal", function (event) {
        if (event.target.id === "modalMenu") {
            showMore.value = false;
        }
    });
</script>

<style scoped>
a {
    text-decoration: none;
    width: 100%;
    display: flex;
    justify-content: center;
}
.btn-circle {
    width: 25px;
    height: 45px;
    border-radius: 50%;
    border: none;
    background: #e0e0e0;
    font-size: 24px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.btn-circle:hover {
    background: #d0d0d0;
}

.extra-options {
    animation: fadeIn 0.3s ease;
}

@keyframes fadeIn {
    from { opacity: 0; transform: translateY(-5px); }
    to { opacity: 1; transform: translateY(0); }
}
</style>